import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LugaresServices } from '../services/lugares.services';
import { Observable } from 'rxjs';
import { FormControl } from '@angular/forms';
import { Http } from '@angular/http';
import { debounceTime, switchMap, map } from 'rxjs/operators';

@Component({
    selector: 'app-crear',
    templateUrl: './crear.component.html'
})

export class CrearComponent {
    id: any = null;
    lugar: any = {};

    private searchField: FormControl;
    result$: Observable<any>;

    constructor(private route: ActivatedRoute, private lugaresServices: LugaresServices, private http: Http) {
        this.id = this.route.snapshot.params['id'];
        if (this.id != 'new') {
            this.lugaresServices.getLugar(this.id)
                .valueChanges().subscribe(lugar => {
                    this.lugar = lugar;
                });
        }

        const URL = 'https://maps.google.com/maps/api/geocode/json';
        this.searchField = new FormControl();
        this.result$ = this.searchField.valueChanges
            .pipe(debounceTime(500),
                switchMap(query => this.http.get(`${URL}?address=${query}`)),
                map(response => response.json()),
                map(response => response.results)
            );
        // this.result$ = this.searchField.valueChanges
        //     .switchmap(query => this.http.get(`${URL}?address=${query}`))
        //     .map(response => response.json())
        //     .map(response => response.results);
    }

    public seleccionarDireccion(direccion) {
        console.log(direccion)
        this.lugar.calle = direccion.address_components[1].long_name+' '+direccion.address_components[0].long_name;
        this.lugar.ciudad = direccion.address_components[4].long_name;
        this.lugar.pais = direccion.address_components[5].long_name; 
    }

    guardarLugar() {
        var direccion = this.lugar.calle + ',' + this.lugar.ciudad + ',' + this.lugar.pais;
        this.lugaresServices.obtenerGeoData(direccion)
            .subscribe((result) => {
                this.lugar.lat = result.json().results[0].geometry.location.lat;
                this.lugar.lng = result.json().results[0].geometry.location.lng;
                if (this.id != 'new') {
                    this.lugaresServices.editarLugar(this.lugar);
                    alert('Negocio Editado con exito');
                    this.lugar = {};
                }
                else {
                    this.lugar.id = Date.now();
                    this.lugaresServices.guardarLugar(this.lugar);
                    alert('Negocio guardado con exito');
                    this.lugar = {};
                }
                this.lugar = {};
            })
    }
}

