import { Component } from '@angular/core';
import { LugaresServices } from '../services/lugares.services';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { AngularFireAuth } from 'angularfire2/auth';

@Component({
    selector: 'app-lugares',
    templateUrl: './lugares.component.html',
    animations: [
        trigger('contenedorAnimable', [
            state('inicial', style({
                opacity: 0,
                backgroundColor: 'green',
                transform: 'rotate3d(0,0,0,0deg)'
            })),
            state('final', style({
                opacity: 1,
                backgroundColor: 'yellow',
                transform: 'rotate3d(5,10,20,30deg)'
            })),
            transition('inicial => final', animate(1000)),
            transition('final => inicial', animate(500))
        ]),
        trigger('contenedorAnimable1', [
            state('inicial', style({
                opacity: 0
            })),
            state('final', style({
                opacity: 1
            })),
            transition('inicial => final', animate(2000)),
            transition('final => inicial', animate(1000))
        ])
    ]
})

export class LugaresComponent {
    title = 'PlatziSquare';
    lat: number = 20.6193569;
    lng: number = -100.4042762;
    lugares = null;
    boMostrarMensaje = false;
    mensajeError = '';

    state = 'inicial';

    animar() {
        this.state = (this.state === 'final') ? 'inicial' : 'final';
    }

    animacionInicia(e) {
        console.log('Iniciado!');
        console.log(e);
    }

    animacionTermina(e) {
        console.log('Terminado!');
        console.log(e);
    }

    constructor(private lugaresServices: LugaresServices, private angularFireAuth: AngularFireAuth) {
        //HTPP
        // angularFireAuth.auth.currentUser.getIdToken(true).then(
        //     (idToken) => {
        //         //FIREBASE HTTP
        //         lugaresServices.getLugares(idToken)
        //             .subscribe(lugares => {
        //                 this.lugares = lugares;
        //                 var me = this;
        //                 me.lugares = Object.keys(me.lugares).map(function (key) {
        //                     return me.lugares[key];      //Conversation the Object to array
        //                 });
        //                 this.state = 'final';
        //             },
        //                 error => {
        //                     //alert('Problemas al cargar los datos. Error: ' + error.statusText);  
        //                     this.boMostrarMensaje = true;           //Errores de carga de datos o codigo mal escrito
        //                     this.mensajeError = 'Problemas al cargar los datos, error: ' + error.statusText;
        //                 });
        //     }
        // )

        //FIREBASE SOTKETS
        this.lugaresServices.getLugares().valueChanges()
            .subscribe(lugares => {
                this.lugares = lugares;
                this.state = 'final';
            },
                error => {
                    //alert('Problemas al cargar los datos. Error: ' + error.statusText);  
                    this.boMostrarMensaje = true;           //Errores de carga de datos o codigo mal escrito
                    this.mensajeError = 'Problemas al cargar los datos, error: ' + error.statusText;
                });
    }

    public cerrarMensaje() {
        this.boMostrarMensaje = false;
        this.mensajeError = '';
    }

}
