import { Injectable } from "@angular/core";
import { AngularFireAuth } from "angularfire2/auth";
import * as firebase from 'firebase/app'
import { Router } from "@angular/router";

@Injectable()

export class AutorizacionService {

    constructor(private angularFireAuth: AngularFireAuth, private router: Router) {
        this.isLogged();
    }

    public facebookLogin() {
        this.angularFireAuth.auth.signInWithPopup(new firebase.auth.FacebookAuthProvider())
            .then((result) => {
                alert('Sesión iniciada con Facebook');
                console.log(result);
                this.router.navigate(['lugares']);
            })
            .catch((error) => {
                console.log('error');
            })
    }

    public login = (email, pwd) => {
        this.angularFireAuth.auth.signInWithEmailAndPassword(email, pwd)
            .then((response) => {
                alert('Sesión iniciada');
                console.log(response)
                this.router.navigate(['lugares']);
            })
            .catch((error) => {
                alert('Existe un error');
                console.log(error);
            })
    };

    public registro = (email, pwd) => {
        this.angularFireAuth.auth.createUserWithEmailAndPassword(email, pwd)
            .then((response) => {
                alert('Usuario registrado con exito');
                console.log(response)
                this.router.navigate(['lugares']);
            })
            .catch((error) => {
                alert('Existe un error');
                console.log(error);
            })
    };

    public isLogged() {
        return this.angularFireAuth.authState;
    }

    public logout() {
        this.angularFireAuth.auth.signOut();
        alert('Sesión Cerrada');
        this.router.navigate(['lugares']);
    }

    public getEmail() {
        return this.angularFireAuth.auth.currentUser.email;
    }

}